<template>
  <div id="my-bgm" class="touch">
    <aplayer autoplay
               theme="pic"
               :mini="mini"
               :volume="0.1"
               repeat="repeat-all"
               :onplay="xxx"
               :music="music[0]" style="float:left;"/>
  </div>
</template>

<script>
// import Aplayer from 'vue-aplayer'
import MusicData from '../../../static/mork/music.json'

export default {
  name: 'MyBGM',
  components: {
    Aplayer: resolve => {
      require(['vue-aplayer'], resolve)
    }
  },
  data () {
    return {
      music: [],
      mini: true
    }
  },
  methods: {
    changeMini () {
      console.log(this.mini)
      this.mini = !this.mini
    },
    xxx () {
      console.log('play')
    }
  },
  created () {
    this.music = MusicData
    document.addEventListener('visibilitychange', function () {
      let audio = document.getElementsByTagName('audio')[0]
      if (document.hidden) {
        audio.pause()
      } else {
        audio.play()
      }
    })
  }
}
</script>

<style scoped>
  #my-bgm{
    position: fixed;
    bottom: 20px;
    left:20px;
  }
  #my-bgm div{
    padding: 0;
  }
</style>



